.main-home{
    height: 50vw;
    background-image: url("../../Images/bgImage.jpg");
    background-size: cover;
    padding: 5em;
    color: white;
    font-family: "roboto", sans-serif;
    font-weight: 1000;
}

.inner-main-home{
    display: flex;
    justify-content: space-between;
}

.left-main-home{
    font-size: 2.5vw;
    display: flex;
    flex-direction: column;
    max-width: 50vw;
}

.right-main-home img{
    height: 30vw;
    width: 45vw;
    border-radius: 2em;
    transition: all 1s;
}

.left-main-home button{
    color: white;
    background-image: url("../../Images/flame.png");
    width: 10em;
    height: 3em;
    border-radius: 1em;
    color: rgb(146, 5, 0)
}

.left-main-home button:hover{
    opacity: .9;
    font-size: 1.01em;
}

.religion-app-para{
    font-size: .8em;
    opacity: .7;
}

.moto-home{
    font-size: 1.5em;
}