.nav-bar{
    display: flex;
    justify-content: space-between;
    /* background-image: url('../../Images/flame.png'); */
    position: sticky;
    top: 1px;
}

ul {
    display: flex;
    justify-content: center;
    font-family: "lato", sans-serif;
    font-weight: 1000;
    font-style: italic;
}

li {
  margin: 0 1em;
}

.navbar-link{
    text-decoration: none;
    transition: all .2s;
}

.navbar-link:hover{
    font-size: 1.1em;
    font-weight: 1000;
}

.app-name{
    margin-left: .5em;
    font-size: 4em;
    font-family: "roboto", sans-serif;
    font-weight: 1000;
    color: white;
}

.app-tabs{
    margin-top: 3vw;
    font-size: 1.5vw;
    color: rgb(146, 5, 0);
    font-weight: 1000;
}

.user-profile{
    margin-top: .5em;
    display: flex;
    font-family: "lato", sans-serif;
    font-weight: 1000;
    color: rgb(146, 5, 0);
    font-style: italic;
    font-size: 2em;
    margin-right: 1em;
}

.user-profile img{
    height: 1.5em;
    width: 1.5em;
    border-radius: 50%;
}

sub{
    position: relative;
    top: 5px;
    font-size: 0.7em;
    font-weight: bolder;
    color: white;
    background-color: red;
    padding-left: 6px;
    padding-right: 6px;
    border-radius: 50%;
}

.cart img{
    height: 1.5em;
    cursor: pointer;
    transition: all .1s;
}

.cart img:hover{
    height: 1.7em;
}