.item-container{
    padding-left: 5em;
    padding-right: 5em;
    display: flex;
    justify-content: space-around;
    gap: 1em;
    flex-wrap: wrap;
}

.item-card{
    display: flex;
    flex-direction: column;
    background-color: rgb(153, 28, 28);
    align-items: center;
    align-content: flex-start;
    border-radius: 2em;
}

.item-card img{
    height: 15em;
    width: 15em;
    border-radius: 2em;
}

.upper-card{
    padding: 20px;
    min-height: fit-content;
    padding-bottom: 0px;
}

.lower-card{
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 1.2em;
    color: white;
}

.item-quantity{
    display: flex;
    gap: 1em;
}

.lower-card button{
    background-color: aliceblue;
    border-radius: 1em;
    margin-bottom: 1em;
    height: 3em;
    width: 12em;
    background-color: rgb(9, 197, 9);
    color: white;
}

.no-pointer{
    pointer-events: none;
    opacity: .8;
}

.lower-card button:hover{
    height: 3.1em;
    width: 12.3em;
    opacity: .8;
}