.cart-display{
    position: absolute;
    top: 0px;
    background-color: #000000a0;
    font-weight: bolder;
    color: white;
    right: 100%;
    transition: all .5s;
    min-height: 100%;
}

.cart-display.show{
    right: 1px;
}

.cart-div{
    display: flex;
    flex-direction: column;
    padding: 2.5em;
    gap: 1em;
}

.cart-item{
    display: flex;
    flex-direction: column;
    background-color: rgb(109, 23, 23);
    padding: 1em;
    border-radius: 1em;
}

.upper-cart-item{
    display: flex;
    gap: 1em;
}

.content{
    font-size: 1.5em;
    display: flex;
    flex-direction: column;
}

.lower-cart-item{
    display: flex;
    gap: 1em;
}

.item-image img{
    height: 7em;
    border-radius: 2em;
}

.cart-item-container{
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.cancel-order{
    background-color: red;
    color: white;
    border-radius: 1em;
    height: 2em;
}

.checkout{
    background-color:green;
    color: white;
    border-radius: 1em;
    height: 2em;
    width: 100%;
}

.cart-header{
    display: flex;
    justify-content: space-between;
}

.cart-header img{
    height: 1.7em;
    position: relative;
    top: .5em;
}

.cart-header h1{
    font-size: 2em;
}

.final-price p{
    font-size: 2em;
}

.cart-div button{
    height: 3em;
}

.cart-div button:hover{
    opacity: .8;
}

.cart-item-container button{
    color: white;
    background-color: rgb(42, 140, 23);
    border-radius: 3em;
    padding-left: 1em;
    padding-right: 1em;
    height: 2em;
}