.footer{
    padding: 1.5em;
    color: white;
    padding-bottom: 1em;
    font-size: 1.3em;
    font-family: "roboto", sans-serif;
    font-weight: 1000;
}

.inner-footer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.upper-footer{
    display: flex;
    justify-content: space-between;
}

.footer-left, .footer-right{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
} 

.chat-buttons{
    display: flex;
    gap: 5px;
}

.chat-buttons img{
    height: 3em;
}

.lower-footer{
    max-width: 60vw;
    display: flex;
    justify-content: space-between;
}

.links{
    display: flex;
    justify-content: space-around;
}

.links img{
    height: 2em;
    border-radius: 50%;
    margin-top: .5em;
    margin-bottom: .5em; 
}

.footer-right button{
    background-color: transparent;
    height: 2em;
    border-radius: 10px;
    width: 100%;
}

.app-name-footer, .question-para{
    font-size: 2em;
    font-weight: 1000;
}

.footer a:hover{
    opacity: 0.8;
}

.lower-footer, .footer-right button{
    font-family: "lato", sans-serif;
    font-weight: 1000;
    color: rgb(146, 5, 0);
    font-style: italic;
}